@mixin flex($justifyContent, $flexDirection, $alignItems) {
  display: flex;
  justify-content: $justifyContent;
  flex-direction: $flexDirection;
  align-items: $alignItems; }

@mixin fortune($width, $height) {
  padding: 0;
  background: no-repeat url("../img/Koleso_s_prizami_2.png");
  background-size: cover;
  background-position: center;
  width: $width;
  height: $height;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
  border-radius: 300px; }

@mixin pointer($margin, $width, $height) {
  padding: 0;
  margin: $margin;
  background: no-repeat url("../img/Strelochka_dlya_Kolesa.png");
  background-size: cover;
  background-position: center;
  width: $width;
  height: $height;
  position: absolute;
  z-index: 2; }

@mixin home($width, $height) {
  padding: 80px 0px 40px 0px;
  margin: 0;
  background: no-repeat url("../img/Fon_Fortuna.jpg");
  width: $width;
  height: $height;
  @include flex(center, column, center); }

.imgPanelHome {
  @include home(100vw, 700px); }

.imgPanelHome.AdditionalAttempts {
  @include home(100vw, 100%); }

.imgFortune {
  @include fortune(300px, 300px); }

.imgFortune.AdditionalAttempts {
  @include fortune(200px, 200px); }

.imgPointer {
  @include pointer((-295px 0px 0px 0px), 20px, 30px); }

.imgPointer.AdditionalAttempts {
  @include pointer((-400px 0px 0px 0px), 20px, 30px); }

.imgPointer.Attempts {
  @include pointer((-295px 0px 0px 0px), 20px, 30px);

